<template>
  <div class="articleContent">
  	<div class="showManageTitle">
  	  文章管理
  	</div> 

  	<div class="articleTab">
  		<div class="tab">
	  		<ul class="tabUl">
	  			<li 
	  			  v-for="(item, index) in tabOptions"
	  			  @click="clickTab(index,item.state)"
	  			  :class="{active: cur === index}"
						:key="index"
	  			>
	  			  {{item.tabName}}
	  		  </li>
	  		</ul>

	  		<div class="tabButton" @click="writeArticle">
	  			<span class="writeAricle">写文章</span>
	  		</div>
	  	</div>

	  	<div class="articleTabContent">
	  		<articleContent :articleState='articleState'></articleContent>
	  	</div>
  	</div>
  </div>
</template>
<style lang="less" scoped>
  .articleContent{
  	margin:60px 0 60px 260px;
  	.showManageTitle{
  	  color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  text-align: left;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;	
  	}
  	.articleTab{
  		.tab{
  			position: relative;
	  		.tabUl{
	  			display: flex;
	  			width: 465px;
					margin: 20px 0 0 40px;
	  			li{
	  				flex: 1;
	  				cursor: pointer;
	  				list-style: none;
	  				height: 48px;
	  				text-align: center;
	  				line-height: 48px;
	  			}
	  			.active{
	  				background: #ececec;
	  				border-top: 2px solid rgb(0,190,222);
	  			}
	  		}
	  		.tabButton{
	  			position: absolute;
	  			right: 50px;
	  			top: 0;
	  			span{
	  				display: inline-block;
	  				width: 200px;
	  				height: 40px;
	  				border-radius: 5px;
	  				border: 1px solid rgb(0,190,222);
	  				line-height: 40px;
	  				cursor: pointer;
	  				color: rgb(0,190,222);
	  			}
	  		}
  		}
  	}
  }
</style>
<script>
  import articleContent from './article/article'

	export default {
	  data () {
	    return {
	      tabOptions: [
 					{
						 tabName: "所有文章",
						 state :0
 					},
 					{
						 tabName: "已发布",
						 state : 3
 					},
 					{
						 tabName: "草稿箱",
						 state : 2
 					}
	      ],
				cur: 0,
				articleState:0
	    }
	  },
	  components: {
	  	articleContent
	  },
	  methods: {
	  	clickTab(index,state) {
				this.cur = index;
				this.articleState = state
	  	},
	  	writeArticle() {
	  		this.$router.push({
	  			path: "/informationManage/writeArticle"
	  		})
	  	}
	  }
	}
</script>


