<template>
  <div class="article">
  	<div class="articleContent">
			<div class="noData" v-if="!articleData[0]">
        暂无数据
      </div>
  	  <div class="articleDetail" v-for="(item,index) in articleData" :key="index"  @click.stop="ToArticleDetail(item.id)">
      	<h1>{{item.title}}</h1>
      	<div class="articleText">
					{{item.content | delHtmlTag}}
        </div>
        <div class="articleMark">
          <span v-if="item.source">来源：{{item.source}}</span>
          <span v-if="item.state==3">{{item.publishTime | getdate}}</span>
					<span v-if="item.state==2">{{item.publishTime | getdate}}</span>
          <!-- <span>截至：2019-5-17 18:00</span> -->
          <div class="operate">
						<el-tooltip class="item" effect="dark" content="发布" placement="top" v-if="item.state==2">
							<img src="../../../assets/images/publish.png" @click.stop="upConfirm(item.id,3)" >
						</el-tooltip>
						<el-tooltip class="item" effect="dark" content="编辑" placement="top">
							 <img src="../../../assets/images/edit.png" @click.stop="ToEdit(item)">
						</el-tooltip>
						<el-tooltip class="item" effect="dark" content="下线" placement="top" v-if="item.state==3">
							<img src="../../../assets/images/down.png" @click.stop="outConfirm(item.id,2)">
						</el-tooltip>
						<el-tooltip class="item" effect="dark" content="删除" placement="top" v-if="item.state==2">
							<img src="../../../assets/images/delete.png" @click.stop="deleteConfirm(item.id)" >
						</el-tooltip>
          </div>
        </div>

        <div class="status publish" :class="{statusStyle:item.state==2}">
        	{{judgeState(item.state)}}
        </div>
   	  </div>	
  	</div>
  	<div class="page">
	    <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange">
			</el-pagination>
		</div>
  </div>
</template>
<style lang="less" scoped>
  .article{
  	padding: 0px 40px;
  	.articleContent{
  		background: #ececec;
			padding: 15px;
			.noData{
				background: #ffffff;
				height: 60px;
				line-height: 60px;
			}
  		.articleDetail{
  			margin-bottom: 20px;
  			background: white;
  			box-shadow: 0 2px 5px #ccc;
  			border-radius: 5px;
  			padding: 20px;
  			text-align: left;
				position: relative;
				cursor: pointer;
  			h1{
  				padding: 0;
  				margin: 0;
  				font-size: 18px;
  				font-weight: normal;
					color: black;
					width: 90%;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
  			}
  			.articleText{
  				margin-top: 20px;
  				color: #808080;
					line-height: 30px;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 3;
					overflow: hidden;
  			}
  			.articleMark{
  				margin-right: 10px;
  				margin-top: 20px;
  				position: relative;
  				span{
  					margin-right: 50px;
  					color: #4a4747;
  				}
  				.operate{
  					position: absolute;
  					right: 40px;
  					bottom: -2px;
  					img{
  						width: 20px;
  						margin-left: 40px;
  						cursor: pointer;
  					}
  				}
  			}
  			.status{
  				position: absolute;
  				text-align: cneter;
  				height: 30px;
  				width: 100px;
  				top: 10px;
  				right: 0;
  				border-radius: 5px 0 0 5px;
  				text-align: center;
  				line-height: 30px;
  				font-size: 12px;
  				cursor: pointer;
  			}
  			.publish{
  				background: rgb(29,176,184);
  				color: white;	 				
  			}
  		}
  	}
  	.page{
  		margin-top: 30px
  	}
  }
</style>
<script>
import Vue from 'vue';
import {mapState} from 'vuex';
import { Popover } from 'element-ui';
Vue.use(Popover);
export default {
	name:'manageText',
	props:{
		articleState:Number
	},
	data(){
		return{
			articleData:[],
			state : this.articleState,
			total:0,
			pageNo:1
		}
	},
	watch: {
		articleState(val){
			this.state = val;
			this.pageNo = 1;
			this.getArticleList()
		}
	},
	computed: {
    ...mapState({
      userInfo:(state)=>state.mutations.userInfo
    })
  },
	created() {
		this.getArticleList()
	},
	methods: {
		handleCurrentChange(val) {
      this.pageNo = val;
      this.getArticleList();
    },
		getArticleList(){
			let url = this.api.configIp + this.net.newsList;
      let userId = this.userInfo.id;
      if(this.EditorTitle==''){
        this.$message.warning('请输入标题!');
        return
      }else if(this.EditorText == ''){
        this.$message.warning('请输入内容!');
        return
      }
			let params = {
				"state":this.state,
				"pageNo":this.pageNo,
				"pageSize": 10,
				"orderByParam": "{publishTime:-1}"
			}
      this.$post(url,params).then((res)=>{
        // console.log('sucess:',res)
				if(res.code == 200 && res.obj.datas != null){
					this.articleData= res.obj.datas
					this.total = res.obj.rowCount
				}
      }).catch((err)=>{
        console.log('err:',err)
      })
		},
		judgeState(state){
			switch(state){
				case 2:
					return '草稿'
					break;
				case 3:
					return '已发布'
					break
			}
		},
		ToArticleDetail(id){
			this.$router.push({path:"/articleDetail",query:{id:id}})
		},
		ToEdit(item){
			this.$router.push({path:"/informationManage/writeArticle",query:{articleData:item}})
		},
		chanageState(id,state){
			let url = this.api.configIp + this.net.newsStateEdit;
      let params = {
        "state":state,
				"id":id,
			}
			console.log(params)
      this.$post(url,params).then((res)=>{
				if(res.code == 200){
					this.getArticleList()
				}
      }).catch((err)=>{
        console.log('err:',err)
      })
		},
		upConfirm(id,state){
			this.$confirm('确定发布该文章吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.chanageState(id,state)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消发布'
        });          
      });
		},
		outConfirm(id,state){
			this.$confirm('确定下线该文章吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.chanageState(id,state)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消下线'
        });          
      });
		},
		deleteConfirm(id){
			this.$confirm('确定删除该文章吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteArticle(id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
		},
		deleteArticle(id){
			let url = this.api.configIp + this.net.newsRemove;
      let params = {
				"id":id,
			}
      this.$post(url,params).then((res)=>{
				if(res.code == 200){
					this.$message.success('删除成功!')
					this.articleData.forEach((item,index) => {
						if(item.id == id){
							this.articleData.remove(item)
						}
					});
				}
      }).catch((err)=>{
        console.log('err:',err)
      })
		}
	},
}
</script>


